


.controls{
    position: relative;
    width: 50vw;
    max-height: 200px;
    height: 22vh;
    margin-left: 0vw;
    margin-top: -22vh;
    z-index: 99999;
    text-align: center;
    padding: 10px;
    /* background-color: #000; */
}


.controls a{
    margin-top: 10px;
    transition: all .15s linear 0s;
    display: inline-block;
    padding: 15px 25px;
    background-color:#faebd7;
    
    font-size: 12px;
    color:#000;
    font-weight:800;
    font-family: arial;
    letter-spacing: 1px;

    box-shadow: -3px 3px 0 #000;
    text-decoration: none;
    border-radius: 16px;
}

.intend {
    position: relative;

    width: 60vw;
    max-height: 200px;
    height: 30vh;
    margin-left: 40vw;


    margin-top: -45vh;
